@import url("../base.css");

.quiz {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100vh;
}

header,
footer {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    padding: 1rem 2rem 0 0;
    gap: 2rem;
}

header > div,
footer > div {
    padding: 2rem;
    border-radius: 8px;
}

.current-round {
    background-color: var(--color-red);
}

.not-current-round {
    background-color: var(--color-div);
}

.question {
    font-size: 4rem;
    text-align: center;
    color: var(--color-green);
    padding: 3rem 0;
    margin: 2rem 1.6rem 0 1.6rem;
    border-radius: 12px;
    background-color: var(--color-div);
}

.timer {
    font-size: 5rem;
    margin-top: 2rem;
    text-align: center;
}

.next:hover {
    cursor: pointer;
}

.finished {
    position: fixed;
    top: 50%;
    left: 50%;
    color: var(--color-green);
    transform: translate(-50%, -50%);
    font-size: 5rem;
}
