@import url("../base.css");

html {
    background-color: var(--color-background);
    color: white;
    font-family: sans-serif;
}

h1:first-child {
    font-size: 50px;
    text-align: center;
}

h1 > div:last-child {
    color: var(--color-green);
}

.welcome-container {
    position: fixed;
    border-radius: 10px;
    margin: 0 auto;
    background-color: var(--color-div);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 10rem 1rem 10rem;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    gap: 2rem;
}

#room-field {
    color: white;
}
